@import url("https://fonts.googleapis.com/css?family=Chivo:300,700|Playfair+Display:700i");
.header {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #003049;
    padding: 0 10%;
}

.logo {
    font-size: 3rem;
    color: white;
    font-weight: bold;
    font-family: 'Playfair Display', serif;

}

.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
}

.header li {
    margin-left: 3rem;
}

.header a {
    text-decoration: none;
    font-size: 1.5rem;
    color:  whitesmoke;
}

.header a:hover,
.header a:active,
.header a.active {
    color: white;
}

.badge {
    background-color: #cc2062;
    color: white;
    border-radius: 12px;
    padding: 0 1rem;
    margin-left: 0.5rem;
}